function getMonth(month) {
  if (month === '01') {
    return ''
  }
  if (month === '02') {
    return ''
  }
  if (month === '03') {
    return ''
  }
  if (month === '04') {
    return ''
  }
  if (month === '05') {
    return ''
  }
  if (month === '06') {
    return ''
  }
  if (month === '07') {
    return ''
  }
  if (month === '08') {
    return ''
  }
  if (month === '09') {
    return ''
  }
  if (month === '10') {
    return ''
  }
  if (month === '11') {
    return ''
  }
  if (month === '12') {
    return ''
  }
}

export function vievDate(date) {
  const day = date.slice(8, 10)
  const month = getMonth(date.slice(5, 7))
  const year = date.slice(0, 4)
  return `${day} ${month} ${year}`
}